<script>
import { ref, watch, $emit } from 'vue'
import { apiGetScheduleList,apiGetPinkScheduleList } from "@/api/api"
import { addZero } from '@/utils/handle'


export default {
  // props: {
  //   date_p: Array
  // },
  setup(props, { emit }) {
    let today = new Date()
    let year = today.getFullYear()
    let month = today.getMonth() + 1
    // const date_p = ref([])

    let scheduleList = ref()
    let start = ref('')
    let end = ref('')
    let dateList = ref()
    let clickDay = ref(today)
    const attrs = ref([
      {
        highlight: {
          color: process.env.VUE_APP_MODE,
          fillMode: "light"
        },
        dates: dateList
      },
      {
        highlight: {
          color: "blue",
          fillMode: "light"
        },
        dates: today
      },
      {
        highlight: {
          color: process.env.VUE_APP_MODE,
          fillMode: "solid",
          contentClass: "italic"
        },
        dates: clickDay
      },
    ])

    //select date style
    const selectAttribute = {
      highlight: {
        style: {
          // Circle styles
          background: "#1F9DDF"
        },
        contentStyle: {
          // Text styles
          color: "white"
        }
      }
    }
    const attributes = []

    const dayclick = (day) => {
      clickDay.value = day.id
      updateDay(day)
    }

    const pageChange = async (page) => {
      // updatePage(page)
      start.value = `${page?.year}/${page?.month}/1`
      start.value = new Date(start.value.replace(/-/g, '/'))
      end.value = new Date(start.value.getFullYear(), start.value.getMonth() + 1, 0)
      start.value = `${start.value.getFullYear()}/${addZero(start.value.getMonth() + 1)}/${addZero(start.value.getDate())}`
      end.value = `${end.value.getFullYear()}/${addZero(end.value.getMonth() + 1)}/${addZero(end.value.getDate())}`
      await searchDateList()
      dateList.value = getDateList(scheduleList.value)
      console.log(dateList.value)
    }

    const updateDay = (val) => {
      emit("updateDay", val)
    }


    //查詢該月份日期
    const searchDateList = async () => {
      try {
        if (process.env.VUE_APP_MODE == 'purple') {
          scheduleList.value = await apiGetScheduleList({ start: start.value, end: end.value })
        } else {
          scheduleList.value = await apiGetPinkScheduleList({ start: start.value, end: end.value })
        }
        scheduleList.value = scheduleList.value.data
        // console.log(scheduleList.value)
      } catch (error) {
        console.log(error)
      }

    }

    //把資料串起來return dateList
    const getDateList = (data) => {
      // console.log("把資料串起來")
      let dateList = []
      if (data.fullDay.length > 0) {
        data.fullDay.forEach(item => {
          dateList = dateList.concat(item)
        })
      }
      if (data.partTime.length > 0) {
        dateList = dateList.concat(data.partTime)
      }
      dateList = dateList.filter((item, index) => dateList.indexOf(item) === index)
      return dateList
    }


    return { attrs, dayclick, year, month, pageChange }
  }
}
</script>

<template>
  <div class="testStyle2">
    <v-calendar class='date-picker' is-expanded :from-page="{ month: month, year: year }" :attributes="attrs"
      @dayclick="dayclick" @update:toPage="pageChange" />
  </div>
</template>

<style lang="scss" scoped>
.date-picker {
  border: 0px;
}
</style>