<script>
import { ref, onMounted, watch } from 'vue'
import ScheduleCalendar from '../schedule/components/ScheduleCalendar.vue'
import NoData from "../../components/global/NoData.vue"
import { useRouter } from "vue-router"
import { addZero } from '@/utils/handle'
import { apiGetScheduleDate, apiGetPinkScheduleDate } from "@/api/api"

export default {
  setup() {
    const router = useRouter()
    let mode = ref('purple')
    let scheduleList = ref()
    let start = ref('')
    let end = ref('')
    let dateList = ref()
    let todayOrder = ref()
    // let page = ref('')

    onMounted(async () => {
      try {
        let today = new Date()
        today = `${today.getFullYear()}/${addZero(today.getMonth() + 1)}/${addZero(today.getDate())}`
        if (process.env.VUE_APP_MODE == 'purple') {
          mode.value = 'purple'
          todayOrder.value = await apiGetScheduleDate({ scheduleDate: today })
        } else {
          mode.value = 'pink'
          todayOrder.value = await apiGetPinkScheduleDate({ scheduleDate: today })
        }
        todayOrder.value = todayOrder.value.data
        // console.log(todayOrder.value)
      } catch (error) {
        console.log(error)
      }
    })

    //點擊日期
    const updateDay = async (val) => {
      // console.log('updateDay', val.id)
      if (process.env.VUE_APP_MODE == 'purple') {
        todayOrder.value = await apiGetScheduleDate({ scheduleDate: val.id.replace(/-/g, '/') })
      } else {
        todayOrder.value = await apiGetPinkScheduleDate({ scheduleDate: val.id.replace(/-/g, '/') })
      }
      todayOrder.value = todayOrder.value.data
      // console.log(todayOrder.value)
    }


    const goto = (val) => {
      router.push({ path: val })
    }

    return { mode, updateDay, scheduleList, dateList, todayOrder, goto }
  },
  components: {
    ScheduleCalendar,NoData
  },
}

</script>

<template>
  <div class="scheduleList">
    <div class="ScheduleCalendar">
      <ScheduleCalendar @update-day="updateDay" :date_p="dateList" />
    </div>
    <div class="calendar-container">
    </div>
    <div class="schedule-container" v-if="(mode == 'purple')">
      <div class="card" v-for="item in todayOrder" :key="item.scheduleId">
        <div class="card-body">
          <a @click="goto(`/schedule/info/${item.scheduleId}`)" class="card-link">
            <div class="card-title">{{ item.name }}({{ item.gender == "Male" ?
                "男性" : "女性"
            }}/{{ item.age }}歲/{{ item.height }}cm/{{ item.weight }}kg)</div>
            <div class="card-location" v-if="(item.servicePlace.placeType.id == 1)">照顧地點:
              {{ item.servicePlace.city.name }}{{ item.servicePlace.city.area }}{{ item.servicePlace.place.address }}
            </div>
            <div class="card-location" v-if="(item.servicePlace.placeType.id != 1)">照顧地點:
              {{ item.servicePlace.place.name }}</div>
            <div class="card-kind">履歷身分: {{ item.serviceName }}</div>
            <div class="card-date">日期時段: {{ item.startTime.substring(0, item.startTime.length - 3) }} ~
              {{ item.endTime.substring(0, item.endTime.length - 3).split(' ')[1] }}</div>
            <div class="card-punch">
              打卡簽到: <span class="notpunch" v-if="(item.punchInType == 0) || (item.punchInType == 2)">未打卡</span>
              <span class="notpunch" v-if="item.punchInType == 1">{{ item.punchInTime }}</span>
              <span class="makeup" v-if="(item.punchInType == 3)">補</span><span class="signin"
                v-if="(item.punchInType == 3)">{{ item.punchInTime
                }}</span><br>
              打卡簽退: <span class="notpunch" v-if="(item.punchOutType == 0) || (item.punchOutType == 2)">未打卡</span>
              <span class="notpunch" v-if="item.punchOutType == 1">{{ item.punchOutTime }}</span>
              <span class="makeup" v-if="(item.punchOutType == 3)">補</span><span class="signout"
                v-if="(item.punchOutType == 3)">{{ item.punchOutTime
                }}</span>
            </div>
          </a>
          <div class="card-state">
            <!-- <a href="javascript:void(0);" class="btn btn-state" v-if="(item.workingStatus == 1)">已確認</a> -->
            <a href="schedule-detail.html" class="btn btn-notwork" v-if="(item.workingStatus == 2)">未到班</a>
            <a href="schedule-detail.html" class="btn btn-notwork" v-if="(item.workingStatus == 4)">中斷</a>
            <a href="schedule-detail.html" class="btn btn-completed" v-if="(item.workingStatus == 1)">已完工</a>
            <a href="schedule-detail.html" class="btn btn-completed" v-if="(item.workingStatus == 3)">待確認完工</a>
          </div>
        </div>
      </div>
      <NoData v-if="todayOrder?.length == 0"/>
    </div>
    <div class="schedule-container" v-if="(mode == 'pink')">
      <div class="card" v-for="item in todayOrder" :key="item.scheduleId">
        <div class="card-body">
          <a @click="goto(`/schedule/info/${item.scheduleId}`)" class="card-link">
            <div class="card-title">{{ item.name }}({{ item.gender == "Male" ?
                "男性" : "女性"
            }}/{{ item.age }}歲/{{ item.height }}cm/{{ item.weight }}kg)</div>
            <div class="card-location" v-if="(item.servicePlace.placeType.id == 1)">照顧地點:
              {{ item.servicePlace.city.name }}{{ item.servicePlace.city.area }}{{ item.servicePlace.place.address }}
            </div>
            <div class="card-location" v-if="(item.servicePlace.placeType.id != 1)">照顧地點:
              {{ item.servicePlace.place.name }}</div>
            <div class="card-kind">履歷身分: {{ item.serviceName }}</div>
            <div class="card-date">日期時段: {{ item.startTime.substring(0, item.startTime.length - 3) }} ~
              {{ item.endTime.substring(0, item.endTime.length - 3).split(' ')[1] }}</div>
            <div class="card-punch">
              打卡簽到: <span class="notpunch" v-if="(item.punchInType == 0) || (item.punchInType == 2)">未打卡</span>
              <span class="notpunch" v-if="item.punchInType == 1">{{ item.punchInTime }}</span>
              <span class="makeup" v-if="(item.punchInType == 3)">補</span><span class="signin"
                v-if="(item.punchInType == 3)">{{ item.punchInTime
                }}</span><br>
              打卡簽退: <span class="notpunch" v-if="(item.punchOutType == 0) || (item.punchOutType == 2)">未打卡</span>
              <span class="notpunch" v-if="item.punchOutType == 1">{{ item.punchOutTime }}</span>
              <span class="makeup" v-if="(item.punchOutType == 3)">補</span><span class="signout"
                v-if="(item.punchOutType == 3)">{{ item.punchOutTime
                }}</span>
            </div>
          </a>
          <div class="card-state">
            <!-- <a href="javascript:void(0);" class="btn btn-state" v-if="(item.workingStatus == 1)">已確認</a> -->
            <a href="schedule-detail.html" class="btn btn-notwork" v-if="(item.workingStatus == 2)">未到班</a>
            <a href="schedule-detail.html" class="btn btn-notwork" v-if="(item.workingStatus == 4)">中斷</a>
            <a href="schedule-detail.html" class="btn btn-completed" v-if="(item.workingStatus == 1)">已完工</a>
            <a href="schedule-detail.html" class="btn btn-completed" v-if="(item.workingStatus == 3)">待確認完工</a>
          </div>
        </div>
      </div>
      <NoData v-if="todayOrder?.length == 0"/>
    </div>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.scheduleList {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}

.ScheduleCalendar {
  padding: 0rem;
}

.schedule-container {
  margin-top: -1.75rem;
}
</style>